import { Component, OnInit } from '@angular/core';
import { ServiciowishlistService } from 'src/app/services/serviciowishlist.service';

@Component({
  selector: 'app-blogsito',
  templateUrl: './blogsito.page.html',
  styleUrls: ['./blogsito.page.scss'],
})
export class BlogsitoPage implements OnInit {

  blogArray: any = [];
  descricpcionsita: any;

  constructor(private blogService: ServiciowishlistService) { }

  ngOnInit() {
    this.traerBlog();
  }


  traerBlog(){
    this.blogService.recuperaBlog().subscribe((data) => {
      this.blogArray = data;
      
    });
  }

}
