  margin-block-start: 0em !important;
  margin-block-end: 0em !important;
}

ion-button {
  --box-shadow: transparent !important;
}

button {
  background-color: transparent !important;
  border: none;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}

ion-searchbar {
  --icon-color: #1ca3c9;
  width: 95%;
  margin-left: 10px;
}

.search-container {
  width: 90vw;
  right: 5vw;
  height: auto;
  max-height: 50vh;
  overflow-y: scroll;
  position: absolute;
  z-index: 99;
}

.line-filter {
  text-align: center;
  line-height: 20px;
  background: #70c2d1;
  padding: 0;
  margin: 0;
}

.header-ios ion-toolbar:last-of-type {
  --border-width: 0;
}

.header-translucent-ios ion-toolbar {
  --opacity: 1;
}

ion-card-title {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

ion-card-content {
  width: 100%;
  height: auto;
  overflow: hidden;
  text-overflow: ellipsis;
}

.textito {
  height: 150px;
}

.blogsitoDesk {
  width: 100%;
  height: auto;
  display: none;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow-y: scroll;
}
.blogsitoDesk .bodyBlog {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 3em 0em;
  gap: 2em;
}
.blogsitoDesk .bodyBlog .taitl-cont {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.blogsitoDesk .bodyBlog .taitl-cont .taitl {
  color: #00a3c7;
  font-size: 1.7em;
}
.blogsitoDesk .bodyBlog .grida-cont {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em;
  padding: 0em 1em;
}
.blogsitoDesk .bodyBlog .grida-cont .carda {
  width: 18em;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 1em;
  box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
  transition: 0.5s all ease-in-out;
}
.blogsitoDesk .bodyBlog .grida-cont .carda img {
  width: 100%;
  height: auto;
}
.blogsitoDesk .bodyBlog .grida-cont .carda .title-blog {
  width: 100%;
  height: auto;
  text-align: center;
  color: #000;
  padding: 0em 1em;
  font-size: 1.1em;
  cursor: pointer;
  transition: 0.3s all ease-in-out;
}
.blogsitoDesk .bodyBlog .grida-cont .carda .title-blog:hover {
  color: #00a3c7;
}
.blogsitoDesk .bodyBlog .grida-cont .carda .textito {
  width: 100%;
  padding: 0em 1em;
  margin-block-start: 0em;
  margin-block-end: 0em;
  height: auto;
  text-align: center;
  color: #000;
  font-size: 0.9em;
}
.blogsitoDesk .bodyBlog .grida-cont .carda .rmore {
  color: #666;
  text-decoration: none;
  width: 100%;
  height: auto;
  padding: 0em 1.2em;
  padding-bottom: 1em;
  transition: 0.3s all ease-in-out;
  text-decoration: underline;
  font-style: italic;
  font-size: 0.9em;
}
.blogsitoDesk .bodyBlog .grida-cont .carda .rmore:hover {
  color: #00a3c7;
}
.blogsitoDesk .bodyBlog .grida-cont .carda:hover {
  box-shadow: rgba(2, 164, 197, 0.8) 0px 5px 24px;
}

@media (min-width: 992px) {
  ion-content {
    display: none;
  }
  .blogsitoDesk {
    display: flex;
  }
}
.blogsitoDesk::-webkit-scrollbar {
  width: 0.35em;
  background-color: transparent;
  height: 0.7em;
}

.blogsitoDesk::-webkit-scrollbar-thumb {
  background-color: rgba(0, 162, 199, 0.4745098039);
  width: 0.25em;
